import { crawlers } from "@/utils/crawler";
import { useStorage } from "@vueuse/core";
import { defineStore } from "pinia";
/**
 * @description: -store
 */
export const useBook = defineStore("book", () => {
  const list = [crawlers.lengku8.meta, crawlers.tbxsw.meta]; // 
  const current = useStorage("bookPlatformCurrent", list[0].name, localStorage); // 
  const bookPlatform = reactive({
    list,
    current
  });
  const settings = useStorage("bookReadSettings", {
    isNight: false,
    // 
    customBg: "#f6f6f6",
    // 
    fontSizeScale: 0 // 
  }, localStorage);
  const mode = computed(() => {
    return {
      night: {
        bg: "#1b1b1b",
        color: "#6e6e6e",
        fontSizeScale: settings.value.fontSizeScale
      },
      light: {
        bg: settings.value.customBg,
        color: "#101010",
        fontSizeScale: settings.value.fontSizeScale
      }
    };
  }); // 
  const currentMode = computed(() => settings.value.isNight ? mode.value.night : mode.value.light); // 
  // 
  const setSetting = (key, value) => {
    settings.value[key] = value;
  };
  return {
    bookPlatform,
    settings,
    mode,
    currentMode,
    setSetting
  };
});